
.manifesto-section {
  text-align: center;
  padding: 4rem 2rem;
  max-width: 1200px;
  margin: 0 auto;

  // @include mq(mobile-sm) {
  //   padding: 2rem 1rem;
  // }

  h3 {
    font-size: 2rem;
    margin-bottom: 1rem;
    line-height: 1.4;
    color: pink;


  }

  p {
    max-width: 720px;
    margin: 2rem auto 0;
    font-size: 1.125rem;
    line-height: 1.8;


  }

  .acf-gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 0.5rem;
    margin-top: 3rem;

    .gallery-item {
      width: 200px;
      height: 200px;
      overflow: hidden;



      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        display: block;
        transition: transform 0.3s ease;


      }

      &:hover img {
        transform: scale(1.05);
      }
    }

    .blob-item {
      clip-path: ellipse(50% 50% at 50% 50%);
    }

    .blob-1,
    .blob-2,
    .blob-3,
    .blob-4 {
      clip-path: inset(10% 5% 15% 5% round 50% 30%);
    }
  }
}

.core-values-section {
  padding: 4rem 1rem;
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
}

.core-values-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  justify-content: center;


}

.core-value-item {
  width: 300px;
  height: 450px;
  padding: 5rem 2rem 2rem 2rem;
  border-radius: 1rem;
  background-size: cover;
  background-position: center;
  color: white;
  position: relative;
  overflow: hidden;
  text-align: left;



  &::before {
    content: '';
    position: absolute;
    inset: 0;
    background: rgba(0, 0, 0, 0.45);
    z-index: 1;
  }

  .core-value-icon,
  .core-value-title,
  .core-value-text {
    position: relative;
    z-index: 2;
  }
}

.core-value-icon {
  width: 80px;
  height: auto;
}

.core-value-title {
  font-size: 1.8rem;
  font-weight: bold;
  margin: 0;


}

.core-value-text {
  font-size: 1rem;
  line-height: 1.6;

}

.coaches {
  padding: 4rem 1rem;
  background-color: #f9f9f9;
  max-width: 1200px;
  margin: 0 auto;
}

.coach-nav {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-bottom: 3rem;
  flex-wrap: wrap;

  .coach-nav-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    text-decoration: none;
    max-width: 90px;

    img {
      width: 64px;
      height: 64px;
      object-fit: cover;
      border-radius: 50%;
      border: 2px solid #ccc;
      transition: border-color 0.3s ease;
    }

    .coach-nav-label {
      margin-top: 0.5rem;
      font-size: 0.85rem;
      color: #444;
    }

    &:hover img {
      border-color: #000;
    }
  }
}

.coach-profile {
  max-width: 800px;
  margin: 0 auto 4rem;
  text-align: center;


  h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }

  img {
    max-width: 300px;
    width: 100%;
    height: auto;
    border-radius: 0.5rem;
    margin-bottom: 1.5rem;
  }

  .coach-info {
    font-size: 1rem;
    line-height: 1.7;
    margin-bottom: 1.5rem;
    text-align: left;
  }

  blockquote {
    font-style: italic;
    border-left: 4px solid #ddd;
    padding-left: 1rem;
    color: #666;
    margin: 1.5rem 0;
    text-align: left;
  }
}

.coach-cert-carousel {
  display: flex;
  overflow-x: auto;
  gap: 1rem;
  padding: 1rem 0;
  scroll-snap-type: x mandatory;

  .cert-slide {
    flex: 0 0 auto;
    width: 160px;
    scroll-snap-align: start;

    img {
      width: 100%;
      border-radius: 0.5rem;
    }
  }
}

.studio-environment-gallery {
  padding: 4rem 1rem;
  max-width: 1200px;
  margin: 0 auto;

  .section-heading {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 2rem;
  }

  .gallery-grid {
    column-count: 1;
    column-gap: 1.5rem;

      @include mq(tablet-sm) {
      column-count: 2;
    }

    @include mq(tablet) {
      column-count: 3;
    }

  }

  .gallery-item {
    position: relative;
    break-inside: avoid;
    margin-bottom: 2rem;
    overflow: hidden;
    border-radius: 10px;

    img {
      width: 100%;
      height: auto;
      display: block;
      border-radius: 10px;
    }

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 40%;
      background: linear-gradient(to top, rgba(0, 0, 0, 0.5), transparent);
      z-index: 1;
    }

    .image-caption {
      position: absolute;
      bottom: 0.5rem;
      left: 0;
      width: 100%;
      padding: 0 1rem;
      font-size: 0.9rem;
      color: #fff;
      text-align: center;
      z-index: 2;
      line-height: 1.4;
    }
  }
}


//-------------------------------facility
.facility-features {
  padding: 4rem 1rem;
  background-color: #f9f9f9;

  .container {
    max-width: 800px;
    margin: 0 auto;
  }

  .section-heading {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 2.5rem;
  }

  .features-grid {
    list-style: none; 
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
    margin:0;

    @include mq(mobile) {
      grid-template-columns: repeat(2, 1fr);
    }

    @include mq(tablet) {
      grid-template-columns: repeat(3, 1fr);
    }

    @include mq(laptop) {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  .feature-item {
    background-color: #fff;
    padding: 2rem 1rem;
    text-align: center;
    border-radius: 8px;
    transition: background 0.3s ease;
    cursor: default;

    .icon {
      height: 40px;
      margin-bottom: 1rem;

      img {
        max-height: 100%;
        filter: grayscale(1);
        opacity: 0.6;
        transition: all 0.3s ease;
      }
    }

    p {
      font-size: 1rem;
      color: #444;
      transition: color 0.3s ease;
    }

    &:hover {
      background-color: #333;

      .icon img {
        filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(7500%) hue-rotate(180deg);
        opacity: 1;
      }

      p {
        color: #fff;
      }
    }
  }
}

//-------------------Rental pricing 

.rental-pricing-section {
  padding: 4rem 1rem;
  max-width: 1200px;
  margin: 0 auto;
  background-color: #f9f9f9;

    .pricing-table {
    display: flex;
    flex-direction: column;
    gap: 2rem;

    @include mq(laptop) {
      flex-direction: row;
      justify-content: space-between;
    }

    .venue-block {
      flex: 1;
      border: 1px solid #ddd;
      padding: 2rem;
      border-radius: 10px;
      background-color: #fff;

      @include mq(laptop) {
        max-width: 48%;
      }

      .venue-title {
        font-size: 1.5rem;
        margin-bottom: 1rem;
        color: #0077b6;
      }

      .rental-fee-table {
        width: 100%;
        margin-bottom: 1.5rem;

        th, td {
          font-size: 0.95rem;
          text-align: left;
        }
      }

      .equipment-description {
        .equipment-grid {
          display: flex;
          flex-direction: column;
          gap: 1.5rem;

          @include mq(tablet) {
            flex-direction: row;
            flex-wrap: wrap;
          }

          .equipment-floor {
            flex: 1 1 45%;

            h5 {
              font-size: 1rem;
              margin-bottom: 0.5rem;
              color: #333;
            }

            .floor-items {
              font-size: 0.95rem;
              line-height: 1.6;
              text-align: left;
            }
          }
        }
      }
    }
  }
}


//---------------contact
.contact-section {
  padding: 3rem 1rem;
  background-color: #fff;

  .contact-wrapper {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    max-width: 1200px;
    margin: 0 auto;
  }

  .contact-left {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;

    .contact-item {
      display: flex;
      align-items: center;
      gap: 1rem;
      line-height: 1.5;

      .icon-wrapper {
        width: 40px;
        height: 40px;
        min-width: 40px;
        border: 1px solid #ccc;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;

        img,
        svg {
          width: 20px;
          height: 20px;
        }
      }

      .contact-text {
        flex: 1;

        span {
          font-weight: bold;
          display: block;
          margin-bottom: 0.2rem;
        }

        p,
        a {
          margin: 0;
          font-size: 1rem;
          color: #333;
        }
      }

      &.line-item {
        align-items: flex-start;

        .contact-text {
          display: flex;
          flex-direction: column;
          gap: 0.25rem;

          .line-id {
            display: inline-flex;
            align-items: center;
            gap: 0.5rem;

            img {
              width: 64px;
              height: auto;
              border-radius: 4px;
              // display: none;
            }
          }
        }
      }
    }
  }

  .contact-right {
    .contact-image-wrapper {
      position: relative;

      .responsive-contact-image {
        width: 100%;
        height: auto;
        border-radius: 12px;
        display: block;
        object-fit: cover;
      }

      .contact-words {
        margin-top: 1.5rem;
        font-size: 1.5rem;
        font-weight: bold;
        color: #004990;
        text-align: center;
        line-height: 1.4;

        @include mq(mobile) {
          font-size: 2rem;
        }
      }
    }
  }

  @include mq(tablet-sm) {
    .contact-wrapper {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
    }

    .contact-left {
      flex: 1 1 45%;
    }

    .contact-right {
      flex: 1 1 50%;

      .contact-image-wrapper {
        .contact-words {
          position: absolute;
          bottom: 1rem;
          right: 1rem;
          background: rgba(255, 255, 255, 0.8);
          padding: 1rem 1.5rem;
          border-radius: 8px;
          font-size: 2rem;
          font-weight: bold;
          color: #004990;
          text-align: right;
          line-height: 1.4;
        }
      }
    }
  }

  img[alt~="Line"][alt~="QR"][alt~="Code"] {
    display: none !important;
  }
}
